                                                <span class="grey--text caption">{{createTime1(image._id)}}</span>
                                                </div>

                                                <v-divider style="margin-top: 12px; margin-bottom: 10px;"></v-divider>
                                                
                                                <div v-if="image.isSettlement !== 1" style=" display: flex; align-items: center; height: 28px;" class="grey--text  caption">
                                                    <span  class="">{{`${image.price}`}}</span>
                                                    <span style="font-weight:bold;" class="black--text">{{`${image.payment}`}}</span>
                                                    <v-spacer></v-spacer>
                                                    <v-btn small class="my-btn" @click="modifyImageDialogPriceOpen(image)">
                                                        <span class=" caption"></span>
                                                    </v-btn>
                                                    <v-btn small class="my-btn" @click="proPayDialogOpen(image)">
                                                        <span class=" caption"></span>
                                                    </v-btn>
                                                </div>
                                                <div v-else style="  display: flex; align-items: center; height: 28px;" class="grey--text  caption">
                                                    <span class="green--text"></span>
                                                    <span style="font-weight:bold;" class="black--text">{{`${image.payment}`}}</span>
                                                    <v-spacer></v-spacer>
                                                    <span>{{createTime2(image.settlementTime)}}</span>
                                                </div>

                                                <div v-if="image.isFinish !== 1" style=" display: flex; align-items: center; height: 28px;" class="grey--text  caption">
                                                    {{``}}
                                                    <v-spacer></v-spacer>
                                                    <v-btn  small class="my-btn" @click="putProImageFinish(image)">
                                                        <span class=" caption"></span>
                                                    </v-btn>
                                                    <v-btn  small class="my-btn" @click="modifyImageDialogOpen(image)" >
                                                        <span class=" caption"></span>
                                                    </v-btn>
                                                    <v-btn  small class="my-btn" @click="deleteProImage( image._id )" :loading="appLoading">
                                                        <span class=" caption"></span>
                                                    </v-btn>
                                                </div>
                                                <div v-else style=" display: flex; align-items: center; height: 28px;" class="grey--text  caption">
                                                    <span  class="green--text"></span>
                                                    <span style="font-weight:bold;" class="black--text"></span>
                                                    <v-spacer></v-spacer>
                                                    <span>{{createTime2(image.finishTime)}}</span>
                                                </div>
            
                                            </div>
                         

                                            <div style=" display: flex; align-items: center; height: 28px;" class="grey--text  caption">
                                                <span v-if="!image.modelId"></span>
                                                <span v-else class="green--text"></span>
                                                <span v-if="!image.modelId"></span>
                                                <span v-else style="font-weight:bold;" class="black--text">{{`${image.modelName}`}}</span>
                                                <v-spacer></v-spacer>
                                                <v-btn v-if="image.isFinish !== 1"  small class="my-btn" @click="arrangeDialogOpen(image, 'model')">
                                                    <span class=" caption"></span>
                                                </v-btn>
                                                <span></span>
                                                <span v-if="!image.renderId"></span>
                                                <span v-else class="green--text"></span>
                                                <span v-if="!image.renderId"></span>
                                                <span v-else style="font-weight:bold;" class="black--text">{{`${image.renderName}`}}</span>
                                                 <v-spacer></v-spacer>
                                                <v-btn v-if="image.isFinish !== 1"  small class="my-btn" @click="arrangeDialogOpen(image, 'render')">
                                                    <span class=" caption"></span>
                                                </v-btn>
                                            </div>

                                        </v-flex>

                                    </v-layout>
                                </v-container>
                            </v-card>
                        </v-flex>

                     
                        </v-layout>
           
                    </v-flex >
                    
                    <!--  -->
                    <v-flex xs12>
                        <v-card flat>
                            <v-card-title style="padding: 0px 16px; height: 48px;">
                                <span class="grey--text caption"></span>

                            </v-card-title>
                        </v-card>
                    </v-flex>
                    <!--  -->
                    <v-flex>
                        <file @close="uploadDialogClose()" :pid="project._id" :fileList="project.referenceFile"></file>
                    </v-flex>

            </v-layout >                  
        </v-container>
        
<!-- image -->
        <v-dialog v-model="imageDialog" persistent max-width="900px">
            <v-card>
                <v-card-title>
                    <span class="title"></span>
                </v-card-title>
                <v-card-text>
                    <v-container grid-list-md>
                        <v-form v-model="imageValid" ref="imageForm" lazy-validation style="width: 100%;">
                            <v-layout wrap>

                                <v-flex xs12 sm6 md4>
                                    <v-select label=""
                                        :items="styleItems"
                                        v-model="image.style" color="yellow darken-2"
                                        :rules="rules.style"
                                    ></v-select>
                                </v-flex>
                                <v-flex xs12 sm6 md4>
                                    <v-select label=""
                                        v-bind:items="designTypeItems"
                                        v-model="image.designType" color="yellow darken-2" :rules="rules.designType" @input=" designTypeChange()">
                                    </v-select>
                                </v-flex>
                                <v-flex xs12 sm6 md4>
                                    <v-select label="" :items="image.designType == '' ? workDesignItems : homeDesignItems" v-model="image.space" :rules="rules.space" color="yellow darken-2" 
                                    ></v-select>
                                </v-flex>
                                <v-flex xs12 sm6 md4>
                                    <v-select label="" :items="image.designType == '' ? workSpaceItems : homeSpaceItems" v-model="image.area" :rules="rules.area" color="yellow darken-2" 
                                    ></v-select>
                                </v-flex>
                                <v-flex xs12 sm6 md4>
                                    <v-select label="" :items="imageTypeItems" v-model="image.imageType" color="yellow darken-2" :rules="rules.imageType"></v-select>
                                </v-flex>
                                <v-flex xs12 sm6 md4>
                                    <v-text-field
                                    label="" color="yellow darken-2" :rules="rules.price" v-model="image.price"
                                    ></v-text-field>
                                </v-flex>

 
                            </v-layout>
                        </v-form>
                    </v-container>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn  flat color="deep-orange"  @click="imageDialogClose()"></v-btn>
                    <v-btn color="yellow" @click="addImage( image )" :loading="this.$store.state.appLoading"></v-btn>
                </v-card-actions>
            </v-card>
         
        </v-dialog>

<!-- image -->
        <v-dialog v-model="modifyDialog" persistent max-width="900px">
            <v-card>
                <v-card-title>
                    <span class="title"></span>
                </v-card-title>
                <v-card-text>
                    <v-container grid-list-md>
                        <v-form v-model="modifyValid" ref="modifyImageForm" lazy-validation style="width: 100%;">
                            <v-layout wrap>

                                <v-flex xs12 sm6 md4>
                                    <v-select label=""
                                        :items="styleItems"
                                        v-model="modifyImage.style" color="yellow darken-2"
                                        :rules="rules.style"
                                    ></v-select>
                                </v-flex>
                                <v-flex xs12 sm6 md4>
                                    <v-select label=""
                                        v-bind:items="designTypeItems"
                                        v-model="modifyImage.designType" color="yellow darken-2" :rules="rules.designType" @input=" designTypeChange()">
                                    </v-select>
                                </v-flex>
                                <v-flex xs12 sm6 md4>
                                    <v-select label="" :items="modifyImage.designType == '' ? workDesignItems : homeDesignItems" v-model="modifyImage.space" :rules="rules.space" color="yellow darken-2" 
                                    ></v-select>
                                </v-flex>
                                <v-flex xs12 sm6 md4>
                                    <v-select label="" :items="modifyImage.designType == '' ? workSpaceItems : homeSpaceItems" v-model="modifyImage.area" :rules="rules.area" color="yellow darken-2" 
                                    ></v-select>
                                </v-flex>
                                <v-flex xs12 sm6 md4>
                                    <v-select label="" :items="imageTypeItems" v-model="modifyImage.imageType" color="yellow darken-2" :rules="rules.imageType"></v-select>
                                </v-flex>
                                <!-- <v-flex xs12 sm6 md4>
                                    <v-text-field
                                    label="" color="yellow darken-2" :rules="rules.price" v-model="modifyImage.price"
                                    ></v-text-field>
                                </v-flex> -->

 
                            </v-layout>
                        </v-form>
                    </v-container>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn  flat color="deep-orange"  @click="modifyImageDialogClose()"></v-btn>
                    <v-btn color="yellow" @click="putProImage()" :loading="this.$store.state.appLoading"></v-btn>
                </v-card-actions>
            </v-card>
         
        </v-dialog>


<!-- image, -->
        <v-dialog v-model="modifyDialogPrice" persistent max-width="400px">
            <v-card>
                <v-card-title>
                    <span class="title"></span>
                </v-card-title>
                <v-card-text>
                    <v-container grid-list-md>
                        <v-form v-model="modifyValid" ref="modifyImageForm" lazy-validation style="width: 100%;">
                            <v-layout wrap>

                                <v-flex xs12 sm12 md12>
                                    <v-text-field label="" color="yellow darken-2" :rules="rules.price" v-model="modifyImage.price" ></v-text-field>
                                    <!--  -->
                                    <v-text-field label="" v-show="false"></v-text-field>
                                </v-flex>

                             </v-layout>
                        </v-form>
                    </v-container>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn  flat color="deep-orange"  @click="modifyImageDialogPriceClose()"></v-btn>
                    <v-btn color="yellow" @click="putProImage()" :loading="this.$store.state.appLoading"></v-btn>
                </v-card-actions>
            </v-card>
         
        </v-dialog>

<!--  -->
        <v-dialog v-model="proPayDialog" persistent max-width="350px">
            <v-card>
                <v-card-title>
                    <span class="title text-xs-center" style="width: 100%"></span>
                </v-card-title>
                     <v-divider></v-divider>
                <v-card-text>
                                                       
                    <v-flex xs12 sm12 md12>
                            <payfilm :pid="project._id" :image="proPayImage" @close="proPayDialogClose()"></payfilm>
                    </v-flex>

                </v-card-text>

            </v-card>
            
        </v-dialog>

<!--  -->
        <v-dialog v-model="arrangeDialog" persistent max-width="350px">
            <v-card>
                <v-card-title>
                    <span class="title text-xs-center" style="width: 100%"></span>
                </v-card-title>
                     <v-divider></v-divider>
                <v-card-text>
                    <v-form v-model="arrangeValid" ref="arrangeForm" lazy-validation style="width: 100%;">                                
                    <v-flex xs12 sm12 md12>
                        <!--  -->
                        <!-- <v-select
                            label=""
                            autocomplete
                            :loading="customer.loading"
                            cache-items
                            :items="customer.items"
                            :rules="[v => !!v || '']"
                            :search-input.sync="search"
                            v-model="customer.select"
                        ></v-select> -->

                        <v-autocomplete
                            :items="customer.items"
                            :rules="[v => !!v || '']"
                            :search-input.sync="search"
                            v-model="customer.select"    
                            item-text="name"
                            label=""
                            placeholder=""
                        ></v-autocomplete>


                    </v-flex>
                    </v-form>

                </v-card-text>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn  flat color="deep-orange"  @click="arrangeDialogClose()"></v-btn>
                    <v-btn  flat @click="putProImgArrange(workType)" :loading="this.$store.state.appLoading"></v-btn>
                </v-card-actions>

            </v-card>
            
        </v-dialog>

        

    </div>
</template>

<script>
import file from '../file'
import payfilm from '../file/pay'
import upload from '../file/upload'

export default {
    name: 'project',
    components:{ file, payfilm, upload },
    props: [],
    data() {
        return {


            loading: false,
            noImage: require('../../assets/22.png'),

            projectName: this.$route.params.pid,

            imageDialog: false,         //
            imageValid: false,

            modifyDialog: false,        //
            modifyDialogPrice: false,   //
            modifyValid: false,
            modifyImage: {
                style: '',
                designType: '',
                space:'',
                area: '',
                imageType: '',
            },

            proPayDialog: false,        //
            proPayValid: false,                
            proPayImage: {},

            arrangeDialog: false,       //
            arrangeValid: false,        
            arrangeImage: {},     
            workType:'',                //model render

            saveDialog: false,          //

            designTypeItems: [
                { text: '', value: ''},
                { text: '', value: ''}
            ],
            homeDesignItems: [     
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''}
            ],
            homeSpaceItems: [
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''}
            ],
            workDesignItems: [
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''}
            ],
            workSpaceItems: [
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''}
            ],

            styleItems: [
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value:''},
                { text: '', value:''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''},
                { text: '', value: ''}
            ],
            imageTypeItems: [
                { text: '', value: ''},
                { text: '', value: ''}
            ],
            image: {
                state: 0,
                style: '',
                designType: '',
                space:'',
                area: '',
                imageType: '',
                price: ''
            },
            rules: {
                space:  [
                    (v) => !!v || '',
                ],
                area: [
                    (v) => !!v || ''
                ],
                price: [
                    (v) => /^[0-9]+$/.test(v) || ',',
                ],
                style: [
                    (v) => !!v || '',
                ],
                designType: [
                    (v) => !!v || ''
                ],
                imageType: [
                    (v) => !!v || ''
                ],
            },
            customer: {
                loading: false,
                items: [],
                select: null,
                idMap: [],
            },
            search: null,
        }
    },
    watch: {
        async search (val) {

            if (!val) return;

            let re=/[^\u4e00-\u9fa5]/;  
            if (re.test(val)) return;
            if (!this.customer.items.indexOf(val)) return;

            let items = await this.$store.dispatch('getRender', {pageSize: 50, currentPage: 1, authority: 'render',  search: this.search });
            console.log(items);
            if (items.count === 0) return;
            this.customer.items = [];
            this.customer.idMap = [];
            for ( let index in items.users) {
                this.customer.items.push(items.users[index].realInformation.name);
                this.customer.idMap.push(items.users[index]._id);
            }

        }
    },
    computed: {
        project() {
            return this.$store.state.project.changeData;
        },
        projects() {
            return this.$store.state.project.listData;
        },
        appLoading() {
            return this.$store.state.appLoading;
        },
        voucher() {
            return this.$store.state.project.voucher;
        }
    },
    methods: {

        //
        imageDialogOpen() {
            this.imageDialog = true;
        },

        //
        imageDialogClose() {
            //form
            this.$refs.imageForm.reset();
            this.imageDialog = false;
        },

        //
        modifyImageDialogOpen(image) {
            this.modifyImage = JSON.parse(JSON.stringify(image));

            this.modifyDialog = true;
        },

        //
        modifyImageDialogClose() {
            //form
            // this.$refs.modifyImageForm.reset();
            this.modifyDialog = false;
        },

        //
        modifyImageDialogPriceOpen(image) {

            this.modifyImage = JSON.parse(JSON.stringify(image));

            this.modifyDialogPrice = true;
            console.log(this.modifyDialogPrice);
        },

        //
        modifyImageDialogPriceClose() {
            //form
            // this.$refs.modifyImageForm.reset();
            this.modifyDialogPrice = false;
        },

        //
        proPayDialogOpen(image) {
            this.proPayImage = JSON.parse(JSON.stringify(image));
            this.proPayDialog = true;
        },

        //
        proPayDialogClose() {
            this.proPayDialog = false;
            // this.$refs.proPayForm.reset();
        },

        //
        arrangeDialogOpen(image, workType) {
            this.arrangeImage = JSON.parse(JSON.stringify(image));
            this.workType = workType
            this.arrangeDialog = true;
        },

        //
        arrangeDialogClose() {
            this.arrangeDialog = false;
            this.$refs.arrangeForm.reset();
        },

        //
        async addImage( image ) {
            
            //
            if(!this.$refs.imageForm.validate()) return;

            //
            await this.$store.dispatch('postProImage', {pid: this.project._id, image});
            //
            await this.$store.dispatch('getProImage', this.project._id);

            this.imageDialogClose();
        },

        //
        async deleteProImage( iid ) {
            console.log(iid);
            //
            await this.$store.dispatch('deleteProImage', {pid: this.project._id, iid});
            //
            await this.$store.dispatch('getProImage', this.project._id);
        },

        //
        async putProImage() {
            //
            await this.$store.dispatch('putProImage', {pid: this.project._id, image: this.modifyImage});
            //
            await this.$store.dispatch('getProImage', this.project._id);
            
            this.modifyImageDialogClose();
            this.modifyImageDialogPriceClose();
        },

        //
        async putProImageFinish(image) {

            //
            await this.$store.dispatch('putProImageFinish', {pid: this.project._id, image});
            //
            await this.$store.dispatch('getProImage', this.project._id);
        },

        //
        async putProImgArrange() {
            //
            if(!this.$refs.arrangeForm.validate()) return;
            
            let uid = this.customer.idMap[this.customer.items.indexOf(this.customer.select)];

             //
            await this.$store.dispatch('putProImgArrange', { workType: this.workType, pid: this.project._id, iid: this.arrangeImage._id, uid });
            //
            await this.$store.dispatch('getProImage', this.project._id)

            this.arrangeDialogClose();
        },

        //
        async projectPay() {
            //
            await this.$store.dispatch('projectPay', {pid: this.project._id, image: this.proPayImage});
        },

        //
        designTypeChange() {
            if (this.image.space) this.image.space = '';
            if (this.image.area) this.image.area = '';
        },


        //
        createTime1(_id) {

            if(!_id) return '';

            let nowYear = new Date();
            let createTime = new Date(parseInt(_id.substring(0, 8),16)*1000);

            return `${createTime.getFullYear()}/${createTime.getMonth() + 1}/${createTime.getDate()} ${createTime.getHours()}:${createTime.getMinutes()}`;

            let year = nowYear.getFullYear() - createTime.getFullYear();     
            if( year >= 1 ) {
                return  `${year}`
            }

            let timeDifference = (new Date().getTime()) - (parseInt(_id.substring(0, 8),16)*1000)

            let dayDifference = Math.round( timeDifference / 1000 / 86400);

            if( dayDifference > 0) {
                return `${dayDifference}`
            }

            let hoursDifference = Math.round( timeDifference / 1000 / 3600);

            if(hoursDifference > 0) {
                return `${hoursDifference}`
            }

            let minutesDifference = Math.round( timeDifference / 1000 / 60);
             
            return  minutesDifference !== 0 ? `${minutesDifference}` : '1';

        },

        createTime2(time) {
            let createTime = new Date(time);
            return `${createTime.getFullYear()}/${createTime.getMonth() + 1}/${createTime.getDate()} ${createTime.getHours()}:${createTime.getMinutes()}`;

        },

        //
        quit(routerName) {
            this.$store.commit('removeChangeDate');
            this.$router.replace({name:'projects'});

        },

        // //
        // savaProject() {
        //     this.$store.dispatch('putProject');
        // },

        //project
        toProjects() {
            this.change('projects')
        },

        //
        change(routerName) {
